@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

.projectContainer {
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0;
    align-items: center;
}

.projectContainer > h1 {
    padding-bottom: 15px;
}

.projectContainer > p {
    padding-bottom: 20px;
}

.projectContainer__projects {
    display: flex;
    width: 80vw;
    margin-top: 50px;
    flex-wrap: wrap;
    justify-content: space-evenly;
}